    // avlidation
    validSec(n) {
      // validate 1 : centre
      if (n == 1) {
        let con = this.centre != "" && this.course != "";
        return con ? true : false;
      }
      // Validate 2 : register
      else if (n == 2) {
        // 1. self validate
        if (this.self) {
          let num =
            this.single.number.length == 10 && /\d/.test(this.single.number);
          // let regex = `/^+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/` ;
          let email = this.single.email != "";
          // &&  this.single.email.match(regex)
          let txt1 = this.single.name.length > 3 && this.single.category != "";

          let txt2 =
            this.single.gender != "" && this.single.qualification.name != "";
          let txt3 =
            this.single.qualification.course != "" &&
            this.single.stream.name != "";
          return num && email && txt1 && txt2 && txt3 ? true : false;
        }
        // 2. Institute validation
        else {
          let a = [];
          this.nomination.forEach((single) => {
            let num =
              single.number.length == 10 && /\d/.test(single.number.length);
            let email = single.email != "";
            // &&  `/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/`.test(single.email)

            let txt1 = single.name.length > 3 && single.category != "";
            let txt2 = single.gender != "" && single.qualification.name != "";
            let txt3 =
              single.qualification.course != "" && single.stream.name != "";

            a.push(num && email && txt1 && txt2 && txt3 ? true : false);
          });

          let test = a.every((x) => x == true);
          // console.log(a)
          // console.log(test)
          // return true;
          return test ? true : false;
        }
      }
      // validate 3 : Area
      else if (n == 3) {
        let con = this.region != "" && this.district != "";
        return con ? true : false;
      }
      // validate 4 : Expectation
      else if (n == 4) {
        return this.institute.expectation != "" ? true : false;
      }
      // validate 5 : Institute
      else if (n == 5) {
        let num =
          this.institute.number.length == 10 &&
          /\d/.test(this.institute.number);
        let email = this.institute.email != "";
        // &&  '/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/'.test(this.institute.email)
        let con1 =
          this.institute.university != "" && this.institute.uni_type != "";
        let con2 =
          this.institute.name.length > 3 &&
          this.institute.address.length > 3 &&
          this.institute.head != "";
        return num && con1 && con2 && email ? true : false;
      } else {
        return true;
      }
