      let raw = await fetch(this.$store.state.api, this.mode);
      let json = await raw.json();
      json.forEach((ele) => {
        if (ele.program == this.centre) {
          this.clearField();
          this.centerID = ele.id;
          this.cen_abt = ele.about;
          this.cen_tit = ele.program;
          // refresh course
          ele.subprogram.forEach((prg) => {
            this.courseOption.push(prg.program);
          });
        }
      });
    },
    async setCourseInfo() {
      if (this.centre != "") {
        // get data
        let url = `${this.$store.state.api}?program=${this.centre}`;
        let raw = await fetch(url, this.mode);
        let json = await raw.json();
        // push data
        json[0].subprogram.forEach((ele) => {
          if (ele.program == this.course) {
            let IPH = this.centerID == 4 && this.course == this.courseOption[0];
            let PV = this.centerID == 4 && this.course == this.courseOption[1];
            let XVR = this.centerID == 4 && this.course == this.courseOption[3];
            let DOST =
              this.centerID == 4 && this.course == this.courseOption[4];

            if (IPH || PV || XVR || DOST) {
              this.instituteOnly = false;
              this.self = false;
              // this.self = false
            } else {
              this.self = true;
              this.instituteOnly = true;
            }

            this.prg_id = ele.id;
            this.courseID = ele.id;
            this.prg_tit = ele.program;
            this.prg_abt = ele.info;
            this.prg_elg = ele.eligibility;
            this.prg_clb = ele.collaborator;
          }
        });
      }
